import React from 'react';
import { Box, Button, makeStyles } from '@material-ui/core';
import { Link } from 'gatsby';
import { Post } from 'models/post';
import { SupportedLanguagesFullName } from 'models/site';
import LanguageFlag from 'components/Utils/LanguageFlag';
import { useCallback } from 'react';
import { linkEvent } from 'utils/loggers';

const useStyle = makeStyles({
  noStyle: {
    margin: 0,
    padding: 0,
    listStyle: 'none',
    textDecoration: 'none',
  },
});

interface Props {
  otherLangPosts: Array<Pick<Post, 'fields'>>;
}
const OtherLangPosts = ({ otherLangPosts }: Props) => {
  const styles = useStyle();
  const handleClick = useCallback((url: string) => {
    linkEvent(url, '언어 변환 클릭');
  }, []);

  return (
    <Box>
      <ul className={styles.noStyle}>
        {otherLangPosts.map(({ fields: post }) => (
          <li className={styles.noStyle} key={post.path}>
            <Link to={post.path} className={styles.noStyle} onClick={() => handleClick(post.path)}>
              <Button variant="outlined" size="large">
                <LanguageFlag lang={post.lang} />
                {SupportedLanguagesFullName[post.lang]}
              </Button>
            </Link>
          </li>
        ))}
      </ul>
    </Box>
  );
};

export default OtherLangPosts;
