import React from 'react';
import WidgetCard from 'components/Cards/WidgetCard';
import { makeStyles } from '@material-ui/core';

const useStyles = makeStyles(({ spacing, typography, shape, palette }) => ({
  root: {
    '& ul': {
      padding: 0,
      margin: 0,
    },
    '& li': {
      padding: 0,
      margin: 0,
      listStyle: 'none',
      '& p': {
        margin: 0,
      },
      '& a': {
        display: 'block',
        padding: `${spacing(1)}px ${spacing(1.5)}px ${spacing(1)}px ${spacing(0.75)}px`,
        marginLeft: spacing(0.75),
        color: palette.grey['800'],
        textDecoration: 'none',
        fontSize: typography.caption.fontSize,
        borderRadius: shape.borderRadius,
        '&:hover': {
          backgroundColor: palette.grey['100'],
        },
      },
    },
    '& li ul': {
      margin: `0 0 ${spacing(1.5)}px ${spacing(1.5)}px`,
      borderLeft: `1px solid ${palette.grey['200']}`,
    },
  },
}));

interface Props {
  tocHtml: string;
}
const TocCard = ({ tocHtml }: Props) => {
  const styles = useStyles();
  return (
    <WidgetCard title="카탈로그">
      <div dangerouslySetInnerHTML={{ __html: tocHtml }} className={styles.root} />
    </WidgetCard>
  );
};

export default TocCard;
