import React from 'react';
import { Card, CardContent, Grid, Chip, makeStyles, Box, Theme } from '@material-ui/core';
import PostHeader from 'components/Post/PostHeader';
import Markdown from 'components/Utils/Markdown';
import RelatedPosts from 'components/Cards/RelatedPosts';
import * as Icon from 'react-feather';
import OtherLangPosts from 'components/Lists/OtherLangPosts';
import { Post } from 'models/post';

const useStyles = makeStyles<Theme>(theme => ({
  thumbnail: {
    paddingTop: '42.8%',
    display: 'block',
    position: 'relative',
    '& img': {
      position: 'absolute',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      width: '100%',
      height: '100%',
      objectFit: 'cover',
      margin: 0,
    },
  },
  languages: {
    margin: `${theme.spacing(4)}px 0`,
  },
  relatedPosts: {
    marginTop: theme.spacing(4),
  },
  tagWrapper: {
    margin: 0,
    padding: 0,
    marginTop: theme.spacing(4),
  },
  tags: {
    listStyle: 'none',
    margin: 0,
    padding: 0,
  },
}));

interface Props {
  post: Post;
  otherLangPosts: Array<Pick<Post, 'fields'>>;
}
const PostCard = ({ post, otherLangPosts }: Props) => {
  const {
    frontmatter: { title, date, categories, tags, thumbnail },
    html,
  } = post;
  const styles = useStyles();

  return (
    <Card>
      {thumbnail && (
        <div className={styles.thumbnail}>
          <img src={thumbnail?.childImageSharp?.fluid?.src} alt={title} title={title} />
        </div>
      )}
      <CardContent component="article">
        <PostHeader title={title ?? ''} date={date} categories={categories ?? []} />
        {otherLangPosts.length > 0 && (
          <Box className={styles.languages}>
            <OtherLangPosts otherLangPosts={otherLangPosts} />
          </Box>
        )}
        <Markdown html={html ?? ''} />
        <section className={styles.relatedPosts}>
          <RelatedPosts tags={tags ?? []} currentPost={post} />
        </section>
        {tags && (
          <Grid container className={styles.tagWrapper} component="ul" spacing={1}>
            <Grid item component="li" className={styles.tags}>
              <Icon.Tag />
            </Grid>
            {tags.map((tag, index) => (
              <Grid item component="li" key={index} className={styles.tags}>
                <Chip label={tag} size="small" />
              </Grid>
            ))}
          </Grid>
        )}
      </CardContent>
    </Card>
  );
};

export default PostCard;
