import React from 'react';
import { graphql } from 'gatsby';
import { Box, makeStyles, Theme } from '@material-ui/core';
import Layout from 'components/Layout/Base';
import SEO from 'components/SEO';
import PostCard from 'components/Cards/PostCard';
import CategoriesCard from 'components/Cards/CategoriesCard';
import TocCard from 'components/Cards/TocCard';
import PostNavigatorCard from 'components/Cards/PostNavigatorCard';
import { Post } from 'models/post';

const useStyles = makeStyles<Theme>(({ spacing }) => ({
  tocWrapper: {
    marginBottom: spacing(2),
  },
  postCardWrapper: {
    marginBottom: spacing(4),
  },
}));

interface Props {
  data: {
    markdownRemark: Post;
    allMarkdownRemark: {
      edges: Array<{ node: Pick<Post, 'fields'> }>;
    };
  };
  pageContext: {
    previous: any;
    next: any;
  };
  location: Location;
}

const PostTemplate = ({ data, pageContext }: Props) => {
  const styles = useStyles();

  const {
    markdownRemark: post,
    allMarkdownRemark: { edges: otherLangPosts },
  } = data;
  const {
    tableOfContents,
    frontmatter,
    excerpt,
    fields: { lang },
  } = post;
  const { previous, next } = pageContext;
  const thumbnail = frontmatter.thumbnail?.childImageSharp?.fluid;

  return (
    <Layout
      right={
        <>
          <Box className={styles.tocWrapper} display={{ xs: 'none', lg: 'block' }}>
            <TocCard tocHtml={tableOfContents ?? ''} />
          </Box>
          <Box>
            <CategoriesCard />
          </Box>
        </>
      }
      rightSticky={true}
    >
      <SEO
        title={frontmatter.title}
        description={frontmatter.description ?? excerpt}
        image={thumbnail?.src}
        lang={lang}
      />
      <Box className={styles.postCardWrapper}>
        <PostCard post={post} otherLangPosts={otherLangPosts.map(v => v.node)} />
      </Box>
      <PostNavigatorCard prev={previous} next={next} />
    </Layout>
  );
};

export default PostTemplate;

export const pageQuery = graphql`
  query PostBySlugQuery($slug: String!, $lang: String!, $postGroup: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(truncate: true)
      html
      tableOfContents(absolute: false, maxDepth: 3)
      fields {
        lang
      }
      frontmatter {
        title
        date(formatString: "YYYY-MM-DD")
        categories
        tags
        thumbnail {
          childImageSharp {
            fluid(maxWidth: 800) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
    allMarkdownRemark(filter: { fields: { postGroup: { eq: $postGroup }, lang: { ne: $lang } } }) {
      edges {
        node {
          fields {
            path
            lang
          }
        }
      }
    }
  }
`;
