import React, { Fragment } from 'react';
import { Box, Divider, makeStyles, Theme } from '@material-ui/core';
import { toKebabCase } from 'utils/string';
import { Link } from 'gatsby';

const useStyles = makeStyles<Theme>(({ spacing, palette, typography }) => ({
  root: {
    marginBottom: spacing(5),
  },
  info: {
    color: palette.grey['500'],
  },
  time: {
    fontSize: typography.caption.fontSize,
    marginRight: spacing(1),
  },
  categoryLink: {
    color: 'inherit',
    textDecoration: 'none',
    fontSize: typography.caption.fontSize,
    '&:hover': {
      color: palette.primary.main,
    },
  },
  title: {
    fontSize: typography.h5.fontSize,
    margin: `${spacing(1)}px 0 ${spacing(3)}px`,
  },
}));

interface Props {
  title: string;
  date: string;
  categories: string[];
}
const PostHeader = ({ title, date, categories }: Props) => {
  const styles = useStyles();
  return (
    <header className={styles.root}>
      <Box className={styles.info}>
        <time className={styles.time}>{date}</time>
        <span>
          {categories.map((category, i) => (
            <Fragment key={i}>
              <Link className={styles.categoryLink} to={`/categories/${toKebabCase(category)}`}>
                {category.toUpperCase()}
              </Link>
              {i !== categories.length - 1 && ' / '}
            </Fragment>
          ))}
        </span>
      </Box>

      <h1 className={styles.title}>{title}</h1>

      <Divider />
    </header>
  );
};

export default PostHeader;
