import React from 'react';
import { Card, Box, CardActionArea, CardContent, makeStyles, Theme } from '@material-ui/core';
import { Post } from 'models/post';
import { Link } from 'gatsby';

const useStyles = makeStyles<Theme>(({ spacing, breakpoints, palette }) => ({
  root: {
    flexWrap: 'wrap',
    justifyContent: 'space-between',
  },
  navCard: {
    width: '100%',
    marginBottom: spacing(2),
    [breakpoints.up('lg')]: {
      width: 'auto',
      marginBottom: 0,
    },
  },
  prev: {
    textAlign: 'right',
  },
  link: {
    color: palette.grey['600'],
    textDecoration: 'none',
  },
}));

interface Props {
  prev?: Post | null;
  next?: Post | null;
}
const PostNavigatorCard = ({ prev, next }: Props) => {
  const styles = useStyles();
  return (
    <Box component="nav" display="flex" className={styles.root}>
      {next ? (
        <Card className={styles.navCard}>
          <Link to={next.fields.path ?? '/'} rel="next" className={styles.link}>
            <CardActionArea>
              <CardContent>
                <span role="img" aria-label="previous">
                  👈
                </span>{' '}
                {next.frontmatter.title}
              </CardContent>
            </CardActionArea>
          </Link>
        </Card>
      ) : (
        <div />
      )}
      {prev ? (
        <Card className={`${styles.navCard} ${styles.prev}`}>
          <Link to={prev.fields.path ?? '/'} rel="prev" className={styles.link}>
            <CardActionArea>
              <CardContent>
                {prev.frontmatter.title}{' '}
                <span role="img" aria-label="next">
                  👉
                </span>
              </CardContent>
            </CardActionArea>
          </Link>
        </Card>
      ) : (
        <div />
      )}
    </Box>
  );
};

export default PostNavigatorCard;
